import type { FC, ReactNode, MutableRefObject } from 'react'
import { FormControl, FormHelperText, FormLabel, InputLabel } from '@material-ui/core'

import { cn } from '@utils/ui'
import useStyles from './styles'

export interface CommonFieldProps {
  classes?: {
    error?: string
    helper?: string
    wrapper?: string
  }
  dataTestId?: string
  disabled?: boolean
  ephemeralError?: boolean // Render error using "absolute" so it does not increase height.
  error?: boolean | string
  id: string
  label?: ReactNode
  showError?: boolean
  touched?: boolean
}

export interface FieldWrapperProps extends CommonFieldProps {
  children: ReactNode | ReactNode[]
  helper?: ReactNode
  LabelComponent?: typeof FormLabel | typeof InputLabel
  ErrorComponent?: FC<FieldErrorProps>
  showHelper?: boolean
  stableErrorWrapper?: boolean
  wrapperRef?: MutableRefObject<HTMLDivElement | null>
  renderBelowHelpers?: () => ReactNode
}

export interface FieldErrorProps {
  className?: string
  ephemeralError?: boolean
  error?: string | boolean | null
}

export function FieldWrapper (props: FieldWrapperProps): JSX.Element {
  const styles = useStyles()
  const {
    id,
    label,
    touched = false,
    error,
    ephemeralError = false,
    showError = touched && Boolean(error),
    helper,
    showHelper = Boolean(helper),
    classes,
    dataTestId = id,
    disabled = false,
    wrapperRef,
    children,
    stableErrorWrapper = false,
    ErrorComponent: FieldError = DefaultError,
    LabelComponent: FieldLabel = InputLabel,
    renderBelowHelpers
  } = props

  const hasLabel = Boolean(label)
  const inputLabelId = `label-for-${id}`

  return (
    <FormControl
      className={cn(classes?.wrapper, disabled ? styles.disabled : '')}
      data-testid={dataTestId} // for React Testing Library
      data-test-id={dataTestId} // for our E2E tests
      disabled={disabled}
      error={showError}
      fullWidth
      ref={wrapperRef}
      variant='filled'
      hiddenLabel={!hasLabel}
    >
      {hasLabel && (
        <FieldLabel htmlFor={id} id={inputLabelId}>
          {label}
        </FieldLabel>
      )}

      {children}

      {(stableErrorWrapper || showError) && (
        <FieldError
          className={classes?.error}
          ephemeralError={ephemeralError}
          error={showError ? error : null}
        />
      )}

      {showHelper && (
        <FormHelperText classes={{ root: cn(styles.fieldHelperText, classes?.helper) }}>
          {helper}
        </FormHelperText>
      )}

      {renderBelowHelpers?.()}
    </FormControl>
  )
}

export default FieldWrapper

// ========================================== //

const DefaultError: FC<FieldErrorProps> = ({
  className,
  ephemeralError = false,
  error
}) => {
  const styles = useStyles()
  return (
    <FormHelperText
      classes={{
        root: cn(styles.fieldHelperText, styles.fieldErrorText, className, ephemeralError && styles.fieldEphemeralError)
      }}
    >
      {error}
    </FormHelperText>
  )
}
