import { ChangeEvent, FocusEvent, ReactNode, SyntheticEvent, useCallback, useEffect, useMemo, useRef, FC } from 'react'
import {
  Autocomplete as MuiAutocomplete,
  AutocompleteChangeReason,
  AutocompleteInputChangeReason,
  AutocompleteProps as MuiAutocompleteProps,
  Value
} from '@material-ui/lab'
import Popper, { PopperProps } from '@material-ui/core/Popper'
import Loading from './components/Loading'

import { cn } from '@utils/ui'
import CLASS_NAMES from './styles'

export type {
  AutocompleteRenderInputParams,
  AutocompleteFreeSoloValueMapping,
  AutocompleteChangeReason
} from '@material-ui/lab'

interface AutocompleteProps<
  T,
  Multiple extends boolean | undefined,
  DisableClearable extends boolean | undefined,
  FreeSolo extends boolean | undefined
> extends MuiAutocompleteProps<T, Multiple, DisableClearable, FreeSolo> {
  options: T[]
  bottomThreshold?: number
  onScroll?: () => void
  onBlur?: (evt: FocusEvent<HTMLDivElement>, val?: string) => void
  onFocus?: (evt: FocusEvent<HTMLDivElement>) => void
  formatValue?: (val?: any) => string
  filterOptions?: (options: T[]) => T[]
  renderOption?: (option: T) => ReactNode
  isExtendedListbox?: boolean
}

const DEFAULT_BOTTOM_THRESHOLD = 100
const SCROLL_TOP_TRIGGER_OFFSET = 100
const MIN_WIDTH_FOR_AUTOCOMPLETE = 350
const INCREASE_WIDTH_COUNT = 150

const LOADING_ENTITY = { id: '__LOADING__', name: '' }

const isLoadingEntity = (val: any): boolean => val?.id === LOADING_ENTITY.id

const CustomPopper: FC<PopperProps> = (props) => {
  const { anchorEl } = props

  const _anchorEl = (typeof anchorEl !== 'function' && anchorEl != null) ? anchorEl : anchorEl?.()
  let popperWidth

  const popoverWidthPlusStep = Number(_anchorEl?.clientWidth) + INCREASE_WIDTH_COUNT

  if (_anchorEl?.clientWidth != null) {
    popperWidth = popoverWidthPlusStep < MIN_WIDTH_FOR_AUTOCOMPLETE
      ? popoverWidthPlusStep
      : Math.max(_anchorEl?.clientWidth, MIN_WIDTH_FOR_AUTOCOMPLETE)
  }

  return (
    <Popper
      {...props}
      placement='bottom-start'
      style={{
        width: popperWidth
      }}
    />
  )
}

const Autocomplete = <
  T,
  Multiple extends boolean | undefined = undefined,
  DisableClearable extends boolean | undefined = undefined,
  FreeSolo extends boolean | undefined = undefined
>({
    id,
    classes,
    options,
    value,
    loading,
    freeSolo,
    bottomThreshold = DEFAULT_BOTTOM_THRESHOLD,
    onScroll,
    onChange: _onChange,
    onInputChange: _onInputChange,
    onBlur: _onBlur,
    onFocus,
    formatValue,
    getOptionLabel = DEFAULT_GET_OPTION_LABEL,
    renderOption: _renderOption = DEFAULT_RENDER_OPTION,
    filterOptions: _filterOptions,
    isExtendedListbox = false,
    ...otherProps
  }: AutocompleteProps<T, Multiple, DisableClearable, FreeSolo>): JSX.Element => {
  const resolvedClasses = useMemo(() => ({
    ...classes,
    option: cn(CLASS_NAMES.option, classes?.option),
    popper: cn(CLASS_NAMES.popper, classes?.popper)
  }), [classes])

  const inputRef = useRef('')
  const prevScrollTopTrigger = useRef(0)

  /**
   * It is used to set inputRef value in onChange handler.
   * Then it is used in onBlur to check if input and component values differ.
   * If they differ we need to select some item from the list of suggestions.
   *
   * Besides inputRef value is passed to props.onBlur handler so that in freeSolo mode we would know the final value.
   *
   * By default if formatValue is not passed it returns empty string which is an old working
   * behaviour before freeSolo mode introduction.
   */
  const convertValueToInputRefValue = useCallback((val?: Value<T, Multiple, DisableClearable, FreeSolo>): string => {
    return formatValue?.(val) ?? ''
  }, [formatValue])

  const resolvedOptions = useMemo(() => {
    return loading === true
      ? [...options, LOADING_ENTITY] as T[]
      : options
  }, [options, loading])

  const renderOption = useCallback((option: T): ReactNode => {
    if (isLoadingEntity(option)) {
      return <Loading />
    }
    return _renderOption(option)
  }, [_renderOption])

  const filterOptions = useCallback((options: T[]): T[] => {
    /**
     * when filterOptions is not provided Autocomplete filters out options automatically
     * by checking option labels with the current input value.
     *
     * As we need to manage loading option here we need to reimplement this automatic
     * logic as a default not to break places where we do not pass filterOptions.
     */
    const optionsAfterPassedFilter = _filterOptions != null
      ? _filterOptions(options)
      : options.filter(opt => {
        const optLabel = getOptionLabel(opt).toLowerCase()
        const inputValue = inputRef.current.toLowerCase()
        return optLabel.includes(inputValue)
      })

    const loadingEntity = options.find(isLoadingEntity)

    /**
     * options passed from outside in filter might add new options instead of adding them
     * directly to options.
     *
     * So, if loading is present in the options in general and it was filtered out in
     * options filtered by the props._filterOptions then we add it to the end.
     */

    return (loadingEntity != null && optionsAfterPassedFilter.find(isLoadingEntity) == null)
      ? [...optionsAfterPassedFilter, loadingEntity]
      : optionsAfterPassedFilter
  }, [_filterOptions, getOptionLabel])

  const listboxProps = useMemo(() => {
    if (onScroll == null) {
      return { ListboxProps: undefined }
    }

    return {
      ListboxProps: {
        onScroll: ({ currentTarget }: SyntheticEvent<HTMLDivElement, UIEvent>) => {
          const { scrollHeight, scrollTop, clientHeight } = currentTarget

          /**
           * we have dynamic loading component in the bottom
           * If delta scrollTop is too small from the previousScrollTop
           * we do not trigger handler
           */
          if (Math.abs(scrollTop - prevScrollTopTrigger.current) <= SCROLL_TOP_TRIGGER_OFFSET) {
            return
          }

          if (scrollHeight - scrollTop <= clientHeight + bottomThreshold) {
            prevScrollTopTrigger.current = scrollTop
            onScroll()
          }
        }
      }
    }
  }, [bottomThreshold, onScroll])

  // ========================================== //
  //                   HANDLERS                 //
  // ========================================== //

  const onChange = useCallback((
    evt: ChangeEvent<{}>,
    value: Value<T, Multiple, DisableClearable, FreeSolo>,
    reason: AutocompleteChangeReason
  ): void => {
    if (_onChange == null || isLoadingEntity(value)) {
      return
    }

    _onChange(evt, value, reason)
  }, [_onChange])

  const onInputChange = useCallback((
    evt: ChangeEvent<{}>,
    val: string,
    reason: AutocompleteInputChangeReason
  ): void => {
    /**
     * 'reset' event is a special thing which needs to be described in details here!
     * It is triggered on the beginning when value is set and when something is selected from the list.
     *
     * We do not set inputRef.current during it as this occurs after onChange and overrides our inputRef change there.
     * This event causes more harm and unintentional side effects. That is why we totaly ignore it here.
     * Instead we have onFocus event handler and enhanced onBlur for better control of the flow
     */
    if (reason === 'reset') {
      return
    }

    inputRef.current = val
    _onInputChange?.(evt, val, reason)
  }, [_onInputChange])

  const onBlur = useCallback((evt: FocusEvent<HTMLDivElement>): void => {
    /**
     * in freeSolo mode we need to let passed onBlur handler to deal with the input value
     */
    if (freeSolo !== true && inputRef.current !== convertValueToInputRefValue(value) && options.length > 0) {
      const filterFunc = formatValue ?? getOptionLabel

      const filteredOptions = filterFunc != null
        ? options.filter((v) => (filterFunc(v)).includes(inputRef.current))
        : options

      if (filteredOptions.length > 0) {
        const typedValue = filteredOptions[0] as Value<T, Multiple, DisableClearable, FreeSolo>
        onChange(evt, typedValue, 'blur')
      }
    }

    _onBlur?.(evt, inputRef.current)
  }, [_onBlur, onChange, convertValueToInputRefValue, formatValue, getOptionLabel])

  // ========================================== //
  //                   EFFECTS                  //
  // ========================================== //

  useEffect(() => {
    inputRef.current = convertValueToInputRefValue(value)
  }, [value])

  // ========================================== //

  return (
    <MuiAutocomplete
      id={id}
      classes={resolvedClasses}
      value={value}
      options={resolvedOptions}
      onChange={onChange}
      onInputChange={onInputChange}
      onBlur={onBlur}
      onFocus={onFocus}
      getOptionLabel={getOptionLabel}
      renderOption={renderOption}
      filterOptions={filterOptions}
      freeSolo={freeSolo}
      autoSelect={false}
      autoHighlight
      PopperComponent={isExtendedListbox ? CustomPopper : undefined}
      {...otherProps}
      {...listboxProps}
    />
  )
}

export default Autocomplete

// ========================================== //

function DEFAULT_GET_OPTION_LABEL (option: any): string {
  return (typeof option === 'string'
    ? option
    : option?.name) ?? ''
}

function DEFAULT_RENDER_OPTION (val: any): ReactNode {
  return typeof val === 'string'
    ? val
    : <p>{val.name}</p>
}
