import {
  ChangeEvent, ComponentType, FocusEvent,
  HTMLAttributes, ReactNode,
  useCallback, useMemo,
  useRef, useState
} from 'react'
import { observer } from 'mobx-react-lite'
import { TextField } from '@material-ui/core'
import {
  AutocompleteChangeReason,
  AutocompleteInputChangeReason,
  AutocompleteCloseReason,
  AutocompleteProps
} from '@material-ui/lab'
import { cn } from '@utils/ui'

import useClickOutside from '@hooks/useClickOutside'
import useClickInside from '@hooks/useClickInside'
import Autocomplete from '@UI/Autocomplete'
import FieldWrapper from '@UI/Form/FieldWrapper'

import useStyles from './styles'

export const SELECT_MAX_ITEMS = 8

export enum DropdownMode {
  Autocomplete = 'dropdown-as-autocomplete',
  Select = 'dropdown-as-select'
}

export interface DropdownProps<T> {
  id: string
  error?: string
  touched?: boolean
  showError?: boolean
  mode?: DropdownMode
  dataTestId?: string
  disablePortal?: boolean

  /**
   * Autocomplete-compatible props
   */
  classes?: AutocompleteProps<T, any, any, any>['classes'] & {
    wrapper?: string
    error?: string
    label?: string
    option?: string
  }
  inputValue?: string
  value: T | null
  options: T[]
  label?: string
  isDropdownOpen?: boolean
  blurOnSelect?: AutocompleteProps<T, any, any, any>['blurOnSelect']
  ListboxComponent?: ComponentType<HTMLAttributes<HTMLElement>>
  NoOptionsComponent?: ComponentType<HTMLAttributes<HTMLElement>>
  DropdownIconComponent?: ComponentType<HTMLAttributes<HTMLElement>> | null
  disabled?: boolean
  loading?: boolean
  disableClearable?: boolean
  openOnFocus?: boolean
  bottomThreshold?: number
  maxLength?: number
  /**
   * if you need to have a dropdown popper larger than the parent component
   */
  isExtendedListbox?: boolean
  /**
   * when you press 'tab' autocomplete onBlur is not working
   * if this prop is true it would set onBlur directly on Autocomplete
   * and all blur cases would be covered.
   * DD-NOTE: use with great caution as using it might break some other use cases.
   */
  isAutocompleteBlurHandlerSet?: boolean
  onScroll?: () => void
  onBlur?: (evt: FocusEvent<HTMLDivElement>) => void
  /**
   * It may be used only in Autocomplete mode.
   * In Select mode there is no input to generate proper 'focus' event
   */
  onInputFocus?: (evt: FocusEvent<HTMLInputElement>) => void
  onChange?: (evt: ChangeEvent<{}>, value: T | null, reason: AutocompleteChangeReason) => void
  onClose?: (evt: ChangeEvent<{}>, reason: AutocompleteCloseReason) => void
  onInputChange?: (
    evt: ChangeEvent<{}>,
    value: string,
    reason: AutocompleteInputChangeReason
  ) => void
  onOpen?: (evt: ChangeEvent<{}>) => void
  getOptionLabel?: (option: T) => string
  getOptionSelected?: (option: T, value: T) => boolean
  getOptionDisabled?: (option: T) => boolean
  filterOptions?: (options: T[]) => T[]
  renderOption?: (option: T) => ReactNode
  startAdornment?: ReactNode
}

export function Dropdown<T> (props: DropdownProps<T>): JSX.Element {
  const {
    id,
    inputValue,
    value,
    options,
    label,
    error,
    touched,
    showError = Boolean(touched) && Boolean(error),
    mode,
    classes,
    dataTestId = id,
    disabled = false,
    loading = false,
    bottomThreshold,
    maxLength,
    openOnFocus,
    isAutocompleteBlurHandlerSet = false,
    blurOnSelect,
    disableClearable,
    disablePortal,
    startAdornment,
    isExtendedListbox,
    ListboxComponent,
    NoOptionsComponent,
    DropdownIconComponent,
    onScroll,
    getOptionSelected,
    getOptionLabel,
    renderOption,
    filterOptions,
    onBlur,
    onInputFocus,
    onChange,
    onClose,
    onInputChange,
    onOpen,
    getOptionDisabled
  } = props

  const styles = useStyles({ increaseInputOverlay: disableClearable === true || value == null })
  const inputRef = useRef<HTMLInputElement>(null)
  const wrapperRef = useRef<HTMLDivElement>(null)
  const [isDropdownOpen, setDropdownOpen] = useState(props.isDropdownOpen ?? false)
  const [isFocused, setFocused] = useState(false)

  const isSelect = (mode === DropdownMode.Select) ||
    ((options.length <= SELECT_MAX_ITEMS) && (mode !== DropdownMode.Autocomplete))

  const hasLabel = Boolean(label)
  const inputLabelClass = hasLabel ? '' : styles.noLabelInput
  const inputSelectClass = isSelect ? styles.inputSelect : ''
  const { wrapper: _1, error: _2, label: _3, ...autocompleteClasses } = classes ?? {}

  const wrapperClasses = useMemo(() => {
    return {
      wrapper: `${classes?.wrapper ?? ''} ${isSelect ? DropdownMode.Select : DropdownMode.Autocomplete}`,
      error: classes?.error
    }
  }, [classes?.wrapper, classes?.error])

  const inputClasses = useMemo(() => ({
    root: `${inputLabelClass} ${inputSelectClass}`
  }), [inputLabelClass, inputSelectClass])

  const noOptionsElement = useMemo(() => {
    if (NoOptionsComponent == null) {
      return undefined
    }

    return <NoOptionsComponent />
  }, [NoOptionsComponent])

  const popupIcon = useMemo(() => {
    if (DropdownIconComponent != null) {
      return <DropdownIconComponent />
    }

    return DropdownIconComponent // also alows null, to render empty
  }, [DropdownIconComponent])

  // ========================================== //
  //                   HANDLERS                 //
  // ========================================== //

  const handleOpenItems = useCallback((evt: ChangeEvent<{}>) => {
    setDropdownOpen(true)
    onOpen?.(evt)
  }, [onOpen])

  const handleCloseItems = useCallback((evt: ChangeEvent<{}>, reason: AutocompleteCloseReason) => {
    setDropdownOpen(false)
    onClose?.(evt, reason)
  }, [onClose])

  /**
   * Makes input unfocusable in the `Select` mode.
   */
  const handleFocusInput = useCallback((evt: FocusEvent<HTMLInputElement>) => {
    if (isSelect) {
      inputRef.current?.blur() // makes input unfocusable
    } else {
      onInputFocus?.(evt)
    }
  }, [isSelect, onInputFocus])

  const handleClickOverlay = useCallback((evt) => {
    evt.preventDefault()

    if (!disabled) {
      setDropdownOpen(isOpen => !isOpen)
      setFocused(true)
    }
  }, [disabled])

  /**
   * Makes dropdown unfocused if click was outside of the field,
   * but not if it was on dropdown's option (which is technically outside).
   */
  const onClickOutside = useCallback((evt: MouseEvent) => {
    const elemId = (evt.target as HTMLElement).getAttribute('id')

    /**
     * in this case normal onBlur is set on the Autocomplete
     */
    if (isAutocompleteBlurHandlerSet) {
      /**
       * Focused state is not always properly managed by autocomplete onBlur
       */
      if (isFocused) {
        setFocused(false)
      }
      return
    }

    /**
     * Click on option inside listbox
     */
    if (elemId?.startsWith(`${id}-option-`) === true) {
      return
    }

    /**
     * Click on listbox (used for empty lisbox or border clicks).
     */
    if (elemId?.startsWith(`${id}-popup`) === true) {
      return
    }

    if (isFocused) {
      setDropdownOpen(false)
      setFocused(false)

      /**
       * It seems Autocomplete uses input's onBlur under the hood.
       * So we need to pass that input to mimic Autocomplete's onBlur.
       * Also, Autocomplete set's "id" to it's internal input, so if we
       * want to use Formik's default `handleBlur`, we should pass input here.
       */
      const target = wrapperRef.current?.querySelector('input') ?? null
      if (target != null) {
        onBlur?.({ target } as any as FocusEvent<HTMLDivElement>)
      }
    }
  }, [onBlur, isFocused, isAutocompleteBlurHandlerSet])

  const onClickInside = useCallback(() => {
    if (!disabled) {
      setFocused(true)
    }
  }, [disabled])

  // ========================================== //
  //                   EFFECTS                  //
  // ========================================== //

  useClickOutside(wrapperRef, onClickOutside)
  useClickInside(wrapperRef, onClickInside)

  // ========================================== //

  return (
    <FieldWrapper
      id={id}
      dataTestId={dataTestId}
      wrapperRef={wrapperRef}
      error={error}
      disabled={disabled}
      showError={showError}
      classes={wrapperClasses}
    >
      <Autocomplete<T, undefined, boolean | undefined>
        classes={autocompleteClasses}
        disableClearable={disableClearable}
        disablePortal={disablePortal}
        blurOnSelect={blurOnSelect}
        /**
         * we can not pass it here because it might crash in v.4*
         * https://github.com/mui/material-ui/issues/31312
         */
        // disabled={disabled}
        getOptionDisabled={getOptionDisabled}
        getOptionLabel={getOptionLabel}
        getOptionSelected={getOptionSelected}
        filterOptions={filterOptions}
        id={id}
        inputValue={inputValue}
        loading={loading}
        ListboxComponent={ListboxComponent}
        noOptionsText={noOptionsElement}
        options={options}
        onChange={onChange}
        onInputChange={onInputChange}
        openOnFocus={openOnFocus}
        open={props.isDropdownOpen ?? isDropdownOpen}
        onOpen={handleOpenItems}
        onClose={handleCloseItems}
        renderOption={renderOption}
        popupIcon={popupIcon}
        value={value}
        bottomThreshold={bottomThreshold}
        onScroll={onScroll}
        isExtendedListbox={isExtendedListbox}
        renderInput={(params) => (
          <div className={cn(styles.inputContainer, disabled ? 'cf-disabled' : '')}>
            {isSelect && (
              <div
                className={styles.inputOverlay}
                onClick={handleClickOverlay}
              />
            )}

            <TextField
              {...params}
              autoComplete='off'
              classes={inputClasses}
              data-testid={`${dataTestId}-input`} // for React Testing Library
              data-test-id={`${dataTestId}-input`} // for our E2E tests
              error={showError}
              focused={isFocused}
              /**
               * we can not pass it here because it might crash in v.4*
               * https://github.com/mui/material-ui/issues/31312
               */
              // disabled={disabled}
              hiddenLabel={!hasLabel}
              inputRef={inputRef}
              inputProps={{
                ...params.inputProps,
                ...(maxLength != null ? { maxLength } : undefined),
                autoComplete: 'off'
              }}
              InputProps={{ ...params.InputProps, startAdornment }}
              label={label}
              onFocus={handleFocusInput}
              variant='filled'
            />
          </div>
        )}
        {
        ...(isAutocompleteBlurHandlerSet ? { onBlur } : {})
        }
      />
    </FieldWrapper>
  )
}

export default observer(Dropdown)
